.image {
    position: absolute;
    top:0;
    left: 0;
    z-index: 1;
    cursor: move;
}
.resizeButton{
    width: 20px;
    height: 20px;
    background: white;
    border: 1px solid black;
    display: inline-block;
}
.br {
    position: absolute;
    bottom: 0;
    right: 0;
    cursor: nw-resize;
}
.tr {
    position: absolute;
    top: 0;
    right: 0;
}


