.screen {
    position: absolute;
    background: rgba(255,255,255,.3);
    z-index: 1;
    cursor:move;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    border: solid rgba(0, 0, 0, .9);
}
.screen input, .screen select {
     cursor:initial;
 }
.screen__removebutton {
    position: absolute;
    top: 0;
    right: 0;
}
.screen input {
    width: 5em;
}
.bordercontrol {
    position: absolute;
}
.bordercontrol input{
    width:3em;
}
.borderinputleft{
    left:0;
    top:45%;
}
.borderinputright{
    right:0;
    top:45%;
}
.borderinputtop{
    top:0;
    left:45%;
}
.borderinputbottom {
    bottom: 0;
    left:45%;
}
.preview {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
}
.screen .content {
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}
.screen .padding {
    padding-bottom: 2em;
}


