#menu {
    position: absolute;
    top:0;
    right: 20px;
    width: 300px;
    min-height: 200px;
    border: 1px solid black;
    background: white;
    z-index: 1000;
}
#result {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    max-height: 80vh;
    overflow-y:auto;
}
#result img{
    max-width: 100%;
    margin:10px;
    border: 1px solid red;
}


